import { Button, Col, Form, InputNumber, Row } from "antd";
import { FacetSelector, IFacetOptions, ISelection } from "./FacetSelector";
import { useState } from "react";
import { Preview } from "./Preview";
import { GetDownloadUrl } from "../utilities/download";

export function MainControls(props: { facets: IFacetOptions[], apiKey: string }) {

    const [selection, setSelection] = useState<ISelection>({
        startYear: 2020,
        endYear: 2025,
        indicators: [],
        transformations: ["L"],
        limit: 0,
        filters: []
    });

    const setLimit = (limit: number | null) => setSelection({ ...selection, limit: limit || 0 }); 

    const setStartYear = (start_year: number | null) => setSelection({ ...selection, startYear: start_year || 5000 });

    const setEndYear = (end_year: number | null) => setSelection({ ...selection, endYear: end_year || 5000 });

    const size = 3; const facetRows = [];
    for (let i = 0; i < props.facets.length; i += size) {
        facetRows.push(props.facets.slice(i, i + size));
    }

    return <Form
        name="basic"
        labelCol={{ span: 8 }}
        wrapperCol={{ span: 16 }}
        initialValues={{ remember: true }}
        autoComplete="off"
    >

        <Row key='options'>
            <Col span={8}>
                <Form.Item
                    key={"limit"}
                    label={"Limit"}
                    name={"limit"}
                    rules={[{ required: false }]}
                >
                    <InputNumber min={0} max={10000} onChange={setLimit} defaultValue={selection.limit} />
                </Form.Item>
            </Col>
            <Col span={8}>
                <Form.Item
                    key={"start_year"}
                    label={"Start Year"}
                    name={"start_year"}
                    rules={[{ required: false }]}
                >
                    <InputNumber min={2000} max={2050} defaultValue={selection.startYear} onChange={setStartYear} />
                </Form.Item>


            </Col>
            <Col span={8}>
                <Form.Item
                    key={"end_year"}
                    label={"End Year"}
                    name={"end_year"}
                    rules={[{ required: false }]}
                >
                    <InputNumber min={2000} max={2050} defaultValue={selection.endYear} onChange={setEndYear} />
                </Form.Item>
            </Col>
        </Row>

        {facetRows.map((facetRow, i) => (<Row key={`row_${i}` }>
            {facetRow.map((facet, col) => (<Col key={`col_${col}`} span={8}><FacetSelector facet={facet} selection={selection} setSelection={setSelection} /></Col>))}
        </Row>))}

        <Button type="primary" onClick={() => startDownload(selection, props.apiKey)} size="large">Download</Button>

        <Preview query={selection} apiKey={props.apiKey}></Preview>
    </Form>
}


function startDownload(selection: ISelection, apiKey: string) {

    const downloadUrl = GetDownloadUrl(selection, apiKey);

    window.location.assign(downloadUrl);
}