import { Form, Space, Switch } from 'antd';
import Select, { MultiValue } from 'react-select'

import makeAnimated from 'react-select/animated';

const animatedComponents = makeAnimated();

export interface IFacetOptions {
    code: string;  
    name: string;
    options: [{
      code: string;
      name: string;
    }]
  }

  
interface IFilter {
    facet: string;
    values: string[];
    summarize: boolean;
}

export interface ISelection {
    startYear: number;
    endYear: number;
    indicators: string[],
    transformations: string[],
    filters: IFilter [],
    limit: number;
}

enum FacetsEnum {
    Transformation = "transformation",
    Indicator = "indicator",
  }


export function FacetSelector(props: { facet: IFacetOptions, selection: ISelection, setSelection: (selection:ISelection) => void }) {

    let filterForFacet = props.selection.filters.filter(f => f.facet === props.facet.code)[0];

    const options = props.facet.options.map(i => ({ value: i.code, label: i.name }));

    const onChange: (e: MultiValue<any>) => void = (e: MultiValue<{value: string}>) => {

        const values = e.map(se => se.value);

        switch(props.facet.code) {
            case FacetsEnum.Transformation:
                props.setSelection({
                    ...props.selection,
                    transformations: values
                });
                break;
            case FacetsEnum.Indicator:
                props.setSelection({
                    ...props.selection,
                    indicators: values
                });
                break;
            default:
                {
                    const otherFilters = props.selection.filters.filter(f => f.facet !== props.facet.code);

                    let existingFilter = props.selection.filters.filter(f => f.facet === props.facet.code);

                    if (existingFilter.length === 0) {
                        existingFilter = [{ facet: props.facet.code, values: values, summarize: false }];
                    }
                    else {
                        existingFilter[0].values = values;
                    }

                    otherFilters.push(...existingFilter);
            
                    const newSelection = {
                        ...props.selection,
                        filters: otherFilters
                    };
                    
                    props.setSelection(newSelection);
                }
        }        
    }

    const onChangeSummary = (checked: boolean, event: React.MouseEvent<HTMLButtonElement>) => {        

        let filterForSummarizeFacet = props.selection.filters.filter(f => f.facet === props.facet.code);

        if (filterForSummarizeFacet.length === 0) {
            
            filterForSummarizeFacet = [{ facet: props.facet.code, summarize: checked, values: [] }]
        }
        else {
            filterForSummarizeFacet[0].summarize = checked;
        }

        const otherFilters = props.selection.filters.filter(f => f.facet !== props.facet.code);

        otherFilters.push(...filterForSummarizeFacet);

        const newSelection = {
            ...props.selection,
            filters: otherFilters
        };
        
        props.setSelection(newSelection);
    }

    const showSummarize = ["indicator", "transformation"].indexOf(props.facet.code) === -1;


    const summarize = filterForFacet && filterForFacet.summarize;

    return <Form.Item
    key={props.facet.code}
    label={props.facet.name}
    name={props.facet.code}
    rules={[{ required: false }]}
>         
            <Space direction="vertical"  style={{ display: 'flex' }}>
                {!summarize && <Select key="filter"
                                closeMenuOnSelect={false}
                                isMulti
                                options={options}
                                components={animatedComponents}
                                onChange={onChange}                            
                                //value={filterForFacet.values}
                            />}
                {showSummarize &&
                    <Switch checkedChildren="Filter" unCheckedChildren="Summarize" onChange={onChangeSummary} />}
            </Space>
        </Form.Item>
  }