import { Alert, Spin, Table } from "antd";
import { ISelection } from "./FacetSelector";
import { GetDownloadUrl } from "../utilities/download";

import { parse } from 'csv-parse/sync';
import { useEffect, useState } from "react";
import { ICancellable, fetchWithTimeout } from "../utilities/utilities";


export function Preview(props: { query: ISelection, apiKey: string }) {

  const downloadUrl = GetDownloadUrl({ ...props.query, limit: 500 }, props.apiKey);

  const [data, setData] = useState([] as object[]);
  const [columns, setColumns] = useState<{ title: string, dataIndex: string, key: string }[] | undefined>(undefined);

  enum StatusEnum { Loading, Ready, Error };
  const [status, setStatus] = useState<StatusEnum>(StatusEnum.Loading);

  const [currentRequest, setCurrentRequest] = useState<ICancellable>()

  const fetch = async () => {
    try {
      setStatus(StatusEnum.Loading);

      // if there is an ongoing request then cancel it.
      if (currentRequest) {
        currentRequest.cancel();
      }

      const request = fetchWithTimeout(downloadUrl);

      setCurrentRequest(request);

      const body = await request.fetch(r => r.text());

      const records: object[] = parse(body, {
        columns: true,
        skip_empty_lines: true
      });

      if (records.length > 0) {
        const newColumns = Object.keys(records[0]).map(field => ({
          title: field,
          dataIndex: field,
          key: field,
        }));
  
        setData(records);
        setColumns(newColumns.sort((a, b) => -a.title.localeCompare(b.title)));
        setStatus(StatusEnum.Ready);
      }
      else {
        setData([]);
        setColumns([]);
        setStatus(StatusEnum.Ready);
      }      
    }
    catch (e) {
      console.log(e);
      setStatus(StatusEnum.Error);
    }
  };

  useEffect(() => {
    fetch()
  }, [props.query]);

  if (status === StatusEnum.Ready) {
    return <Table dataSource={data} columns={columns} />;
  }
  else if (status === StatusEnum.Loading) {
    return <Spin tip="Loading" size="large">
          <div className="content" />
        </Spin>
  }
  else if (status === StatusEnum.Error) {
    return <Alert
          message="Error"
          description="An error occurred loading the page."
          type="error"
          showIcon
        />
  }
  else {
    return <div>unexpected status: {StatusEnum[status]}</div>;
  }
}