import React, { useEffect, useState } from 'react';

import { Alert, Layout, Space, Spin } from 'antd';
import 'antd/dist/reset.css';

import './App.css';
import { Content } from 'antd/es/layout/layout';
import { IFacetOptions } from './components/FacetSelector';
import { fetchWithTimeout } from './utilities/utilities';
import { MainControls } from './components/MainControls';
import config from './config.json'
import queryString from 'query-string';

enum StatusEnum { Loading, Ready, Error };

const contentStyle: React.CSSProperties = {
  color: '#000',
  backgroundColor: '#fff',
};

function App() {
  return <Space direction="vertical" style={{ width: '100%' }} size={[0, 48]}>
    <Layout>
      <Content style={contentStyle}>
        <AppConfig></AppConfig>
      </Content>
    </Layout>

  </Space>
}

function AppConfig() {

  
  const parsed = queryString.parse(window.location.search);


  var idToken = parsed.idToken;  

  if (idToken && typeof(idToken) === 'string') {
    return <AppContent token={idToken}></AppContent>
  }
  else {
    const loginUrl = `${config.loginBaseUrl}/account/signIn?returnUrl=${window.location}&returnToken=true`;

    
    window.location.assign(loginUrl);

    return <>Redirecting to authenticate</>
  }
}


function AppContent(props: { token: string }) {

  const [status, setStatus] = useState<StatusEnum>(StatusEnum.Loading);

  const [facets, setFacets] = useState<IFacetOptions[]>([])

  const fetchUserData = async () => {
    try {
      const request = fetchWithTimeout(`${config.apiBaseUrl}/api/tradeprism/v1/filter_options?token=${props.token}`);
      const data = await request.fetch(r => r.json());      

      setFacets(data);
      setStatus(StatusEnum.Ready);
    }
    catch (e) {
      console.log(e);
      setStatus(StatusEnum.Error);
    }
  }

  useEffect(() => {
    fetchUserData()
  }, []);

  switch(status) {
    case StatusEnum.Loading:
      return <div className="App">
        {status === StatusEnum.Loading && <Spin tip="Loading" size="large">
          <div className="content" />
        </Spin>}
      </div>;
    case StatusEnum.Error:
      return <div className="App"><Alert
          message="Error"
          description="An error occurred loading the page."
          type="error"
          showIcon
        /></div>;
    case StatusEnum.Ready:
      return (
        <div className="App">      
          <MainControls facets={facets} apiKey={props.token}></MainControls>
    
        </div>
      );
    default:
      const x: never = status;

      return <div className="App">      
         <Alert
          message="Error"
          description={"The page entered an unexpected state: " + x}
          type="error"
          showIcon
        />
      </div>;
  }  
}

export default App;
